import "./style.scss"

class RateButton {
  constructor({ classId }) {
    this.classItem = "rate-button__item";
    this.classItemSelected = "rate-button__item_selected";
    this.classId = classId;

    this.itemSelected = 0;

    this._setupRateButtonEvent();
  }

  getItemData() {
    return this.itemData;
  }

  _selectRate(item, items) {
    // if item is the same as selected - toggle class
    if (item.classList.contains(this.classItemSelected)) {
      this.itemData = 0;
      return item.classList.remove(this.classItemSelected);
    }

    for (const _item of items) {
      _item.classList.remove(this.classItemSelected);
    }

    this.itemData = parseInt(item.getAttribute("data"), 10);
    item.classList.add(this.classItemSelected);
  }

  _setupRateButtonEvent() {
    const items = document.querySelectorAll(`.${this.classId} .${this.classItem}`);

    for (const item of items) {
      item.addEventListener("click", (e) => {
        e.preventDefault();
        e.stopPropagation();

        this._selectRate(item, items);
      });
    }
  }
}

// new RateButton({ classId: "rate-button-1" });

export default RateButton;