import "../checkbox";

import "./style.scss";

export default class ListOfCheckboxes {
  constructor(classId) {
    this.classId = classId;
    this.expandableElem = document.querySelector(`.${classId}`);
    this.headerElem = document.querySelector(`.${classId} .list-of-checkboxes__header`);

    this.openClass = "list-of-checkboxes_close";

    if (this.expandableElem === null) throw new Error(`list of checkboxes with id ${classId} not found`);
    if (this.headerElem === null) throw new Error(`list of ckeckboxes header with id ${classId} not found`);

    this._setHeaderEvent();
  }

  _setHeaderEvent() {
    this.headerElem.addEventListener("click", (e) => {
      e.preventDefault();
      e.stopPropagation();

      this.toggleContent()
    });
  }

  toggleContent() {
    if (this.expandableElem.classList.contains(this.openClass)) {
      this.expandableElem.classList.remove(this.openClass);
      return;
    }

    this.expandableElem.classList.add(this.openClass);
  }
}