import DateRange, { textFieldModes } from "../../components/date_range";
import Dropdown, { types } from "../../components/dropdown";
import initSlider from "../../components/range_slider";
import ListOfCheckboxes from "../../components/list_of_checkboxes";

import "./style.scss";

export default function searchRoomFormInit() {
  new DateRange({
    classId: "id-date-range-room-selector",
    textFieldMode: textFieldModes.single
  });

  new Dropdown({
    classId: "id-dropdown-room-finder-guests",
    type: types.guests,
    defaultMessage: " "
  });

  new Dropdown({
    classId: "id-dropdown-room-finder-rooms",
    type: types.rooms,
    defaultMessage: " "
  });

  initSlider({
    classId: "id-range-slider-search-room-form", 
    minValue: 1000,
    maxValue: 20000,
    startValue: 5000,
    endValue: 10000
  });

  const expandable = new  ListOfCheckboxes("id-expandable-checkbox-list-finder");
  expandable.toggleContent();
}
