import "./style.scss";
import "nouislider/distribute/nouislider.min.css";
import noUiSlider from "nouislider";

let valueLower;
let valueUpper;

function _priceFormat(price) {
  return Math.floor(price)
    .toString()
    .split("")
    .reverse()
    .map((ch, index) => (index + 1) % 3 === 0 ? ` ${ch}` : ch)
    .reverse()
    .join("") + "";
}

function setValues([low, hight]) {
  valueLower.innerHTML = _priceFormat(low);
  valueUpper.innerHTML = _priceFormat(hight);
}

export default function initSlider({ classId, minValue, maxValue, step, startValue, endValue }) {
  const sliderElem = document.querySelector(`.${classId} .slider__inner`);

  valueLower = document.querySelector(`.${classId} .slider__value-lower`);
  valueUpper = document.querySelector(`.${classId} .slider__value-upper`);

  const _minValue = minValue || 0;
  const _maxValue = maxValue || 50000;
  const _startValue = startValue || _minValue;
  const _endValue = endValue || _maxValue;

  const slider = noUiSlider.create(sliderElem, {
    start: [_startValue, _endValue],
    connect: true,
    step: step || 100,
    range: {
      min: _minValue,
      max: _maxValue
    },
  });

  setValues([_minValue, _maxValue]);

  slider.on("slide", (values) => setValues(values));
}
