import "./style.scss";

const directions = {
  left: "left",
  right: "right"
}

class Carousel {
  constructor({ classId }) {
    this.classId = classId;

    const rootElem = document.querySelector(`.${classId}`);

    if (rootElem === null)
      throw new Error(`Carousel error: elem with classId ${classId} not found`);

    this.classIndicatorActive = "carousel__indicator_active";

    this._init();
  }

  _init() {
    this.actionsElems = document.querySelectorAll(`.${this.classId} .carousel__side-actions > div`);
    this.indicatorsElems = document.querySelectorAll(`.${this.classId} .carousel__indicators > div`);
    this.itemsElem = document.querySelector(`.${this.classId} .carousel__items`);

    this.itemsCount = this.indicatorsElems.length;

    this.state = {
      currentSlidePosition: 0,
    }

    this._setupActionsEvents();

    this._render();
  }

  _setupActionsEvents() {
    const [leftButton, rightButton] = this.actionsElems;

    leftButton.addEventListener("click", (e) => {
      e.preventDefault();
      e.stopPropagation();

      this._slide(directions.left);
    });

    rightButton.addEventListener("click", (e) => {
      e.preventDefault();
      e.stopPropagation();

      this._slide(directions.right);
    });
  }

  _setState(newState) {
    this.state = newState;
    this._render();
  }

  _render() {
    // this.itemsElem.style.left = `-${100 * this.state.currentSlidePosition}%`;
    this.itemsElem.style.transform = `translateX(-${100 * this.state.currentSlidePosition}%)`;

    for (let i = 0; i < this.itemsCount; i += 1) {
      this.indicatorsElems[i].classList.remove(this.classIndicatorActive);
      if (i === this.state.currentSlidePosition) {
        this.indicatorsElems[i].classList.add(this.classIndicatorActive);
      }
    }
  }

  _slide(direction) {
    let nextSlidePosition;
    if (direction === directions.right) {
      nextSlidePosition = this.state.currentSlidePosition + 1 >= this.itemsCount
        ? 0
        : this.state.currentSlidePosition + 1;
    }

    if (direction === directions.left) {
      nextSlidePosition = this.state.currentSlidePosition - 1 < 0
        ? this.itemsCount - 1 : this.state.currentSlidePosition - 1;
    }

    this._setState({ currentSlidePosition: nextSlidePosition });
  }
}

export default Carousel;

