import "../carousel";
import Carousel from "../carousel/index";
import "../rate_button";

import "./style.scss";

function _priceFormat(index) {
  const elem = document.querySelector(`.id-carousel-${index} + .room-item__info .room-item__info-cost span:nth-child(1)`);

  const formattedPrice = elem.innerHTML
    .toString()
    .split("")
    .reverse()
    .map((ch, index) => (index + 1) % 3 === 0 ? ` ${ch}` : ch)
    .reverse()
    .join("");

  elem.innerHTML = formattedPrice;
}

export default function roomItemInit(index) {
  _priceFormat(index);
  new Carousel({ classId: `id-carousel-${index}` });
}